<template>
  <div class="admin-page">
    <div class="admin-header">
      <h1>채팅 관리</h1>
      <button class="btn-logout" @click="logout">로그아웃</button>
    </div>
    <div class="filters">
      <label>System <input v-model="filters.systemId" placeholder="e.g. cointutor" /></label>
      <label>User <input v-model="filters.userId" placeholder="e.g. 12345" /></label>
      <label>From <input v-model="filters.fromDate" type="date" /></label>
      <label>To <input v-model="filters.toDate" type="date" /></label>
      <button class="btn-search" @click="doSearch">검색</button>
    </div>
    <p v-if="error" class="error">{{ error }}</p>
    <table class="conv-table">
      <thead>
        <tr>
          <th>System</th>
          <th>User</th>
          <th>Name</th>
          <th>Created</th>
          <th>Synced</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="c in conversations"
          :key="c.conversation_id"
          class="row-click"
          @click="loadMessages(c)"
        >
          <td>{{ c.system_id }}</td>
          <td>{{ c.user_id }}</td>
          <td>{{ c.name || '-' }}</td>
          <td>{{ formatDate(c.created_at) }}</td>
          <td>{{ formatDate(c.synced_at) }}</td>
        </tr>
      </tbody>
    </table>
    <p v-if="!loading && conversations.length === 0" class="empty">No conversations match.</p>
    <div v-if="selectedConv" class="messages-box">
      <h3>Messages: {{ selectedConv.name || selectedConv.conversation_id }}</h3>
      <div v-for="m in messages" :key="m.message_id" :class="['message', 'msg-' + m.role]">
        <strong>{{ m.role === 'user' ? 'User' : 'Bot' }}</strong> {{ formatDate(m.created_at) }}<br>
        {{ m.content }}
      </div>
    </div>
  </div>
</template>

<script>
const TOKEN_KEY = 'admin_token'

export default {
  name: 'Admin',
  data() {
    return {
      conversations: [],
      messages: [],
      selectedConv: null,
      filters: { systemId: '', userId: '', fromDate: '', toDate: '' },
      loading: false,
      error: ''
    }
  },
  mounted() {
    const token = localStorage.getItem(TOKEN_KEY)
    if (!token) {
      this.$router.replace('/login')
      return
    }
    this.doSearch()
  },
  methods: {
    authHeaders() {
      const token = localStorage.getItem(TOKEN_KEY)
      return token ? { 'Authorization': 'Bearer ' + token } : {}
    },
    async doSearch() {
      this.error = ''
      this.loading = true
      const params = new URLSearchParams()
      if (this.filters.systemId.trim()) params.set('system_id', this.filters.systemId.trim())
      if (this.filters.userId.trim()) params.set('user_id', this.filters.userId.trim())
      if (this.filters.fromDate) params.set('from_date', this.filters.fromDate)
      if (this.filters.toDate) params.set('to_date', this.filters.toDate)
      try {
        const r = await fetch('/v1/cache/conversations?' + params.toString(), { headers: this.authHeaders() })
        if (r.status === 401) {
          localStorage.removeItem(TOKEN_KEY)
          this.$router.replace('/login')
          return
        }
        if (!r.ok) throw new Error(await r.text())
        this.conversations = await r.json()
        this.selectedConv = null
        this.messages = []
      } catch (e) {
        this.error = e.message
      } finally {
        this.loading = false
      }
    },
    async loadMessages(c) {
      this.error = ''
      try {
        const r = await fetch('/v1/cache/conversations/' + encodeURIComponent(c.conversation_id) + '/messages', { headers: this.authHeaders() })
        if (r.status === 401) {
          localStorage.removeItem(TOKEN_KEY)
          this.$router.replace('/login')
          return
        }
        if (!r.ok) throw new Error('Failed to load messages')
        this.messages = await r.json()
        this.selectedConv = c
      } catch (e) {
        this.error = e.message
      }
    },
    formatDate(iso) {
      if (!iso) return '-'
      try {
        const d = new Date(iso)
        return isNaN(d.getTime()) ? iso : d.toLocaleString()
      } catch (_) { return iso }
    },
    logout() {
      localStorage.removeItem(TOKEN_KEY)
      this.$router.replace('/login')
    }
  }
}
</script>

<style scoped>
.admin-page { padding: 1rem; max-width: 1200px; margin: 0 auto; }
.admin-header { display: flex; justify-content: space-between; align-items: center; margin-bottom: 1rem; }
.admin-header h1 { margin: 0; font-size: 1.25rem; }
.btn-logout { padding: 0.4rem 0.75rem; background: #64748b; color: #fff; border: none; border-radius: 6px; cursor: pointer; }
.filters { display: flex; flex-wrap: wrap; gap: 0.5rem; margin-bottom: 1rem; }
.filters label { display: flex; align-items: center; gap: 0.25rem; }
.filters input { padding: 0.35rem; }
.btn-search { padding: 0.4rem 0.75rem; background: #2563eb; color: #fff; border: none; border-radius: 6px; cursor: pointer; }
.conv-table { border-collapse: collapse; width: 100%; }
.conv-table th, .conv-table td { border: 1px solid #ccc; padding: 0.4rem 0.6rem; text-align: left; }
.conv-table th { background: #f0f0f0; }
.row-click { cursor: pointer; }
.row-click:hover { background: #f9f9f9; }
.error { color: #c62828; }
.empty { color: #666; }
.messages-box { margin-top: 1rem; }
.messages-box h3 { margin-bottom: 0.5rem; }
.message { padding: 0.5rem; margin-bottom: 0.25rem; border-radius: 4px; white-space: pre-wrap; }
.msg-user { background: #e3f2fd; }
.msg-assistant { background: #f3e5f5; }
</style>
